import React, { useEffect, useState } from 'react'
import { StaticImage } from 'gatsby-plugin-image'
import { Tabs, TabList, TabPanels, Tab, TabPanel, Select } from '@chakra-ui/react'

import Box from '../../../components/Box'
import Flex from '../../../components/Flex'
import Text from '../../../components/Text'
import ArrowDown from '../../../components/ArrowDown'
import { Media, responsive } from '../../../contexts/responsive'

const tabs = ['基本調查範圍', '魚類調查範圍', '無脊椎動物調查範圍', '底質調查範圍']
const panels = [
  {
    src: <StaticImage src="base.png" />,
    mobile: <StaticImage src="base_mobile.png" />,
    text: '由佈線員拉出100公尺長的穿越線（穿越線鋪設的深度區間須盡量控制在正負一公尺範圍內），\n並劃分4個子樣區，每個子樣區為20公尺，並於子樣區之間間隔5公尺跳過不予記錄'
  },
  {
    src: <StaticImage src="fish.png" />,
    mobile: <StaticImage src="fish_mobile.png" />,
    text: '在穿越線左右各2.5公尺內緩緩地直線前進，\n觀察礁盤上方5公尺內是否有指標性魚類活動'
  },
  {
    src: <StaticImage src="invertebrate.png" />,
    mobile: <StaticImage src="invertebrate_mobile.png" />,
    text: '由於指標性無脊椎動物均為底棲性，善於躲藏，調查範圍在穿越線左右各2.5公尺內以Z字型、頭下腳上的姿勢搭配手電筒進行地毯式搜索，在礁岩縫隙之間尋找指標性無脊椎動物的蹤跡，同時作環境衝擊的記錄'
  },
  {
    src: <StaticImage src="substrate.png" />,
    mobile: <StaticImage src="substrate_mobile.png" />,
    text: '專注在穿越線上，在樣區內每隔50公分以探棒點在礁盤上，記錄該底質類別，\n每個子樣區會記錄40個點，總計會有160筆資料'
  },
]

const Survey = ({ fullpageApi, isActive }) => {
  const [show, setShow] = useState()
  const [range, setRange] = useState(0)
  useEffect(() => {
    if(isActive) { setTimeout(() => setShow(true), 750)}
  } ,[isActive])
  return (
    <Box className='section' bg="custom.skyBlue">
      <Box opacity={show ? 1 : 0} transition="all 1s" px={responsive('1.75em', "9em")}>
        <Text.Bold fontSize={responsive('1.5em', '3.5em')}>體檢計畫的調查範圍與方式</Text.Bold>
        <Media lessThan='md'>
          <Select borderRadius={0} boxShadow={'-5px 3px 5px #CBDCEB'} bg="white" onChange={(e) => setRange(e.target.value)}>
            {tabs.map((tab, i) => (
              <option value={i} key={tab}>{tab}</option>
            ))}
          </Select>
          <Box.Relative mt="1em" boxShadow={'-5px 3px 5px #CBDCEB'} borderRadius="1em" px="1.5em" pt="1.5em" pb="3em" bg="white">
            <Box width="80%" mx="auto">{panels[range].mobile}</Box>
            <Text mt="1.25em">{panels[range].text}</Text>
            <Box.Absolute right="-1em" bottom="-2em" width="36%">
              <StaticImage src="surveydeco.png" />
            </Box.Absolute>
          </Box.Relative>
        </Media>
        <Box.Relative as={Media} greaterThanOrEqual='md'>
          <Tabs
            bg="white"
            boxShadow={'-5px 3px 5px #CBDCEB'}
            borderRadius="2em"
            overflow={'hidden'}
          >
            <TabList>
              {tabs.map((tab, i) => (
                <Tab
                  px="0"
                  py="0.75em"
                  flex={1}
                  width={'10rem'}
                  bg="custom.tabBg"
                  color="custom.tabColor"
                  fontSize={'1.75em'}
                  _selected={{ bg: 'custom.oceanBlue', color: 'white'}}
                  _hover={{ bg: 'custom.oceanBlue', color: 'white'}}
                  borderLeft={i && '1px solid white'}
                  key={tab}
                >
                  {tab}
                </Tab>
              ))}
            </TabList>
            <TabPanels>
              {panels.map((d, i) => (
                <TabPanel py="4.25em" px="6.25em" key={i}>
                  {d.src}
                  <Text mt="2.4em" fontSize={'1.75em'} whiteSpace={'pre-wrap'}>{d.text}</Text>
                </TabPanel>
              ))}
            </TabPanels>
          </Tabs>
          <Box.Absolute right="-7em" bottom="-5.625em" width="20.5%">
            <StaticImage src="surveydeco.png" />
          </Box.Absolute>
        </Box.Relative>
      </Box>
      <ArrowDown fullpageApi={fullpageApi} />
    </Box>
  )
}

export default Survey
