import React, { useContext, useEffect, useMemo, useState } from 'react'
import { Center } from "@chakra-ui/react"

import Box from '../Box'
import Text from '../Text'
import ArrowDown from '../ArrowDown'
import BubbleAnime from '../SwingAnime'
import BackgroundImage from '../BackgroundImage'
import { Media, responsive } from '../../contexts/responsive'

import bubble_bg from './bubble_bg.svg'
import bubble_bg_mobile from './bubble_bg_mobile.svg'

const IntroModule = ({ fullpageApi, title, text, cover, mobileCover, isActive }) => {
  const [show, setShow] = useState()
  const [appear, setBubble] = useState()
  useEffect(() => {
    if(isActive) {
      setTimeout(() => setBubble(true), 750)
      setTimeout(() => setShow(true), 1500)
    }
  }, [isActive])
  return (
    <Box position={'relative'} className="section">
      <Box.Absolute as={Media} greaterThanOrEqual='md' top="0" left="0" right="0">
        <BackgroundImage src={cover} height="100vh" ratio={1920 / 1080} />
      </Box.Absolute>
      <Box.AbsCenter as={Media} lessThan='md' width="100%">
        <BackgroundImage src={mobileCover} height="100vh" ratio={768 / 736} />
      </Box.AbsCenter>
      <Box.AbsCenter opacity={appear ? 1 : 0} transition="all 1s" as={Media} greaterThanOrEqual='md' width="50%" maxWidth="58em">
        <BubbleAnime>
          <BackgroundImage src={bubble_bg} ratio={940.84 / 774.27} />
        </BubbleAnime>
      </Box.AbsCenter>
      <Box.AbsCenter opacity={appear ? 1 : 0} transition="all 1s" as={Media} lessThan='md' width="110%" maxWidth="30em" minWidth="25em">
        <BubbleAnime>
          <BackgroundImage src={bubble_bg_mobile} ratio={423.38 / 418.31} />
        </BubbleAnime>
      </Box.AbsCenter>
      <Center
        opacity={show ? 1 : 0}
        transition="all 1s"
        color="white"
        flexDirection={'column'}
        position={'relative'}
      >
        <Text.Bold lineHeight="1" textAlign="center" letterSpacing="0.25rem" fontSize={responsive('2em', '3.5vw')} whiteSpace="pre-wrap">{title}</Text.Bold>
        <Box maxWidth={responsive('15rem', '33.33vw')} letterSpacing="0.25rem" fontSize={responsive('0.9375em', '1.46vw')} mt={responsive('1em', '1.75 rem')} whiteSpace="pre-wrap">
          {text}
        </Box>
      </Center>
      <ArrowDown fullpageApi={fullpageApi} />
    </Box>
  )
}

export default IntroModule
