import React, { useEffect, useState } from 'react'
import { StaticImage } from 'gatsby-plugin-image'

import Box from '../../../components/Box'
import Flex from '../../../components/Flex'
import Text from '../../../components/Text'
import ArrowDown from '../../../components/ArrowDown'
import { Media, responsive } from '../../../contexts/responsive'

import StepSVG from './StepSVG'

const legends = [
  { color: 'custom.prepareBlue', text: '行前準備' },
  { color: 'custom.surveyBlue', text: '下水調查' },
  { color: 'custom.finishYellow', text: '完成調查' },
]

const Step = ({ fullpageApi, isActive }) => {
  const [show, setShow] = useState()
  const [step, setStep] = useState()
  useEffect(() => {
    if(isActive) { setTimeout(() => setShow(true), 750)}
  } ,[isActive])
  return (
    <Box className='section' bg="custom.skyBlue">
      <Box opacity={show ? 1 : 0} transition="all 1s" px={responsive('1.75em', "9em")}>
        <Text.Bold fontSize={responsive('1.5em', '3.5em')}>體檢計畫 6 大調查步驟</Text.Bold>
        <Media lessThan='md'>
          <Text fontWeight={500} fontSize={'1em'}>透過「穿越線調查法」來紀錄珊瑚礁的健康狀態！</Text>
          <Box.Relative mt="1em" boxShadow={'-5px 3px 5px #CBDCEB'} borderRadius="1em" pl="1.875em" pr="7.5em" py="1.375em" bg="white">
            <StepSVG width="90%" setStep={setStep} step={step} />
            <Box.Absolute right="-1em" bottom="-1em" width="36%">
              <StaticImage src="deco.png" />
            </Box.Absolute>
          </Box.Relative>
        </Media>
        <Media greaterThanOrEqual='md'>
          <Box.Relative boxShadow={'-5px 3px 5px #CBDCEB'} pt="4em" pb="5.625em" px="6.25em" bg="white" borderRadius="1em">
            <Flex justifyContent={'space-between'}>
              <Text.Bold fontSize={'1.875em'}>透過「穿越線調查法」來紀錄珊瑚礁的健康狀態！</Text.Bold>
              <Flex>
                {legends.map((d, i) => (
                  <Flex alignItems={'center'} ml={i && '0.75em'} key={i} >
                    <Box width="1.5em" height="1.5em" bg={d.color} border="2px solid" mr="0.75em" />
                    <Text lineHeight={0} fontSize={'1.625em'}>{d.text}</Text>
                  </Flex>
                ))}
              </Flex>
            </Flex>
            <Box mt="3.5em">
              <StaticImage src="step.png" />
            </Box>
            <Box.Absolute right="-7em" bottom="-5.625em" width="20.5%">
              <StaticImage src="deco.png" />
            </Box.Absolute>
          </Box.Relative>
        </Media>
      </Box>
      <ArrowDown fullpageApi={fullpageApi} />
    </Box>
  )
}

export default Step
