import React, { useEffect, useState } from 'react'
import { Tabs, TabList, TabPanels, Tab, TabPanel, Circle, Select } from '@chakra-ui/react'
import { StaticImage } from 'gatsby-plugin-image'

import Box from '../../../components/Box'
import Flex from '../../../components/Flex'
import Text from '../../../components/Text'
import BackgroundImage from '../../../components/BackgroundImage'
import ArrowDown from '../../../components/ArrowDown'
import { Media, responsive } from '../../../contexts/responsive'

import pic1 from './pic1.jpeg'
import pic2 from './pic2.jpeg'
import Taiwan from './Taiwan'

const tabs = ['整理介紹', '北海岸', '東海岸', '綠島', '蘭嶼', '墾丁', '小琉球', '澎湖']

const panels = [
  {
    content: [
      {
        title: '每年會在何時、何處進行珊瑚礁體檢？',
        moblieTitle: '每年會在何時、\n何處進行珊瑚礁體檢？',
        text: '台灣珊瑚礁體檢計畫自2009年開始，每年目標在每個區域至少完成6次的珊瑚礁體檢（包含3處樣點、各2個深度（淺處：2-6米，深處：6-12米））。\n在台灣選定了7個區域，2020年達到最佳的成績為對24個樣點進行體檢、一共佈設了47條穿越線。'
      }
    ],
    pics: [pic1, pic2],
    photoBy: '*照片由謝宇拍攝、提供'
  },
  {
    content: [
      {
        title: '野柳',
        text: <><b>保護區｜</b>位於萬里水產動植物繁殖保育區內，作為其他非保護區的對照組，藉以了解保護區與非保護區之間的的珊瑚礁健康狀況是否有差異</>
      },
      {
        title: '潮境公園',
        text: <><b>保護區｜</b>位於望海巷潮境海灣資源保育區內，作為其他非保護區的對照組，藉以了解保護區與非保護區之間的的珊瑚礁健康狀況是否有差異</>
      },
      {
        title: '番仔澳',
        text: <><b>開發案｜</b>番仔澳位於瑞芳保護區內，因台電深澳發電廠預計於基隆的番仔澳灣興建卸煤碼頭而設立此樣點，持續監測該區珊瑚礁健康狀況</>
      },
      {
        title: '鼻頭角、龍洞1.5號、龍洞4號',
        text: <><b>休閒潛水、觀光與漁業壓力｜</b>此處為休閒潛水之勝地，因遊客眾多造成環境壓力，因此需持續監測</>
      },
    ],
  },
  {
    content: [
      {
        title: '石梯坪',
        text: <><b>其他｜</b>受到在地社區營造及部落工作者的邀請而前往設置樣點，而石梯坪也是花蓮少數有珊瑚礁的地方</>
      },
      {
        title: '基翬',
        text: <><b>開發案｜</b>此處為東海岸重要珊瑚礁區域，但被劃為遊樂區，後續衍生出寶盛水族生態遊樂區（俗稱寶盛開發案），預計於此處進行動工</>
      },
      {
        title: '杉原',
        text: <><b>開發案｜</b>因此處海灣具有豐富的珊瑚礁，而杉原海灣開發案（俗稱美麗灣開發案、美麗灣度假村爭議）則預計於此處進行開發</>
      },
    ],
  },
  {
    content: [
      { title: '柴口', },
      { title: '公館鼻', },
      { title: '將軍岩', },
      { title: '石朗', },
      { title: '龜灣', },
      { title: '大白沙', },
    ],
    desc: <><b>休閒潛水、觀光與漁業壓力｜</b>此處為台灣熱門觀光景點，因遊客眾多造成環境壓力，其中柴口、石朗兩樣點被劃為保護區，想同時了解在保護區與非保護區之間的珊瑚礁健康狀況是否有差異</>
  },
  {
    content: [
      { title: '玉女岩', },
      { title: '母雞岩', },
      { title: '南獅', },
    ],
    desc: <><b>休閒潛水、觀光與漁業壓力｜</b>此處為台灣熱門觀光景點，因遊客眾多造成環境壓力，因此需持續監測</>
  },
  {
    content: [
      { title: '合界', text: <><b>休閒潛水、觀光與漁業壓力｜</b>此處為台灣熱門觀光景點，因遊客眾多造成環境壓力，因此需持續監測</> },
      { title: '後壁湖', text: <><b>其他｜</b>在地的潛水店希望認養樣點並自主監測，因當地為熱門的水域遊憩活動景點，遊客眾多造成環境壓力</>},
    ],
  },
  {
    content: [
      { title: '美人洞', },
      { title: '漁埕尾', },
      { title: '厚石群礁', },
    ],
    desc: <><b>休閒潛水、觀光與漁業壓力｜</b>此處為台灣熱門觀光景點，因遊客眾多造成環境壓力，同時居民也多以漁業為生，因此需持續監測    </>
  },
  {
    content: [
      { title: '東嶼坪西側', },
      { title: '東嶼坪南側', },
      { title: '東嶼坪東側', },
    ],
    contentII: [
      { title: '山水', },
      { title: '杭灣', },
    ],
    desc: <><b>休閒潛水、觀光與漁業壓力｜</b>此處為台灣熱門觀光景點，因遊客眾多造成環境壓力，同時居民也多以漁業為生，因此需持續監測</>,
    descII: <><b>其他｜</b>在地的潛水店希望認養樣點並自主監測，因當地為熱門觀光景點，遊客眾多造成環境壓力</>
  },
]

const Detail = ({ fullpageApi }) => {
  const [region, setRegion] = useState(null)
  const [show, setShow] = useState()
  useEffect(() => setShow(true) ,[region])
  // console.log(findIndex(tabs, (o) => o === region))
  return (
    <Box className='section' bg="custom.skyBlue">
      <Flex alignItems={'center'} px={responsive('1em', '8em')}>
        <Box width={responsive(0, '34%')}>
          <Taiwan region={region} />
        </Box>
        <Box pl={responsive(0, '3.375em')} maxWidth={responsive("33em", 'unset')} mx={responsive('auto', 'unset')} flex={1}>
          <Text.Bold fontSize={responsive('1em', '3.5em')}>7大海洋區域樣點分布在哪裡？</Text.Bold>
          <Media lessThan='md'>
            <Select fontSize={'0.875em'} height="2.5em" borderRadius={0} boxShadow={'-5px 3px 5px #CBDCEB'} bg="white" onChange={(e) => setRegion(e.target.value)}>
              {tabs.map((tab, i) => (
                <option value={i} key={tab}>{tab}</option>
              ))}
            </Select>
            <Box.Relative mx="auto" mt="1em" boxShadow={'-5px 3px 5px #CBDCEB'} borderRadius="1em" px="1.5em" pt="1.125em" pb="3em" bg="white">
              {panels[+region]?.content.map((t, k, { length }) => (
                <Box pt={k && '0.75em'} mt={k && '0.75em'} borderTop={k && '1px solid'} borderColor="custom.tabBg" key={k}>
                  <Flex>
                    {length > 1 && <Circle mr="0.25em" size="1.125em" bg="black" fontSize={'1em'} color="white">{k + 1}</Circle>}
                    <Text.Bold whiteSpace="pre-wrap" fontSize={'1em'}>{t.moblieTitle || t.title}</Text.Bold>
                  </Flex>
                  <Text fontSize={'0.875em'} whiteSpace="pre-wrap">{t.text}</Text>
                </Box>
              ))}
              {panels[+region]?.pics && (
                <Box mt="0.75em" borderRadius="2em" overflow={'hidden'} maxWidth="16.5em" mx="auto">
                  <BackgroundImage src={panels[+region]?.pics?.[0]} ratio={303 / 187} backgroundPosition="100% 0%" />
                </Box>
              )}
              {panels[+region]?.photoBy && <Text mt="1rem" textAlign={'right'} fontSize={'0.875em'}>{panels[+region]?.photoBy}</Text>}
              {panels[+region]?.desc && (
                <Box pt={'0.75em'} mt={'0.75em'} borderTop={'1px solid'} borderColor="custom.tabBg">
                  <Text fontSize={'0.875em'} whiteSpace="pre-wrap">{panels[+region]?.desc}</Text>
                </Box>
              )}
              {panels[+region]?.contentII?.map((t, k, { length }) => (
                <Box pt={'0.75em'} mt={'0.75em'} borderTop={'1px solid'} borderColor="custom.tabBg" key={k}>
                  <Flex>
                  {length > 1 && <Circle mr="0.25em" size="1.125em" bg="black" fontSize={'1em'} color="white">{k + panels[+region]?.content.length + 1}</Circle>}
                    <Text.Bold whiteSpace="pre-wrap" fontSize={'1em'}>{t.moblieTitle || t.title}</Text.Bold>
                  </Flex>
                </Box>
              ))}
              {panels[+region]?.descII && (
                <Box pt={'0.75em'} mt={'0.75em'} borderTop={'1px solid'} borderColor="custom.tabBg">
                  <Text fontSize={'0.875em'} whiteSpace="pre-wrap">{panels[+region]?.descII}</Text>
                </Box>
              )}
              <Box.Absolute right="-1em" bottom="-2em" width="36%">
                <StaticImage src="surveydeco.png" />
              </Box.Absolute>
            </Box.Relative>
          </Media>
          <Media greaterThanOrEqual='md'>
            <Tabs
              onChange={(index) => {
                setRegion(tabs[index])
                setShow(false)
              }}
              bg="white"
              boxShadow={'-5px 3px 5px #CBDCEB'}
              borderRadius="1em"
              overflow={'hidden'}
              orientation="vertical"
              flexDirection={'row-reverse'}
            >
              <TabList>
                {tabs.map((tab, i) => (
                  <Tab
                    px="0"
                    py="0.75em"
                    flex={1}
                    width={'10rem'}
                    bg="custom.tabBg"
                    color="custom.tabColor"
                    fontSize={'1.75em'}
                    _selected={{ bg: 'custom.oceanBlue', color: 'white'}}
                    _hover={{ bg: 'custom.oceanBlue', color: 'white'}}
                    borderTop={i && '1px solid white'}
                    key={tab}
                  >
                    {tab}
                  </Tab>
                ))}
              </TabList>
              <TabPanels>
                {panels.map((d, i) => (
                  <TabPanel opacity={show ? 1 : 0} transition="all 1s" px="3.75em" py="3em" key={i}>
                    {d.content.map((t, k, { length }) => (
                      <Box pt={k && '1.25em'} mt={k && '1.25em'} borderTop={k && '1px solid'} borderColor="custom.tabBg" key={k}>
                        <Flex>
                          {length > 1 && <Circle mr="0.25em" size="1.125em" bg="black" fontSize={'2.125em'} color="white">{k + 1}</Circle>}
                          <Text.Bold fontSize={'2.25em'}>{t.title}</Text.Bold>
                        </Flex>
                        <Text fontSize={'1.5em'} whiteSpace="pre-wrap">{t.text}</Text>
                      </Box>
                    ))}
                    <Flex justifyContent={'space-between'}>
                      {d?.pics?.map((pic, k) => (
                        <Box mt="1.5em" width="48%" borderRadius="2em" overflow={'hidden'} key={k}>
                          <BackgroundImage src={pic} ratio={343 / 264} />
                        </Box>
                      ))}
                    </Flex>
                    {d.photoBy && <Text mt="1rem" textAlign={'right'} fontSize={'1.375em'}>{d.photoBy}</Text>}
                    {d.desc && (
                      <Box pt={'1.25em'} mt={'1.25em'} borderTop={'1px solid'} borderColor="custom.tabBg">
                        <Text fontSize={'1.5em'} whiteSpace="pre-wrap">{d.desc}</Text>
                      </Box>
                    )}
                    {d?.contentII?.map((t, k, { length }) => (
                      <Box pt={'1.25em'} mt={'1.25em'} borderTop={'1px solid'} borderColor="custom.tabBg" key={k}>
                        <Flex>
                          {length > 1 && <Circle mr="0.25em" size="1.125em" bg="black" fontSize={'2.125em'} color="white">{k + 1 + d?.content.length}</Circle>}
                          <Text.Bold fontSize={'2.25em'}>{t.title}</Text.Bold>
                        </Flex>
                      </Box>
                    ))}
                    {d.descII && (
                      <Box pt={'1.25em'} mt={'1.25em'} borderTop={'1px solid'} borderColor="custom.tabBg">
                        <Text fontSize={'1.5em'} whiteSpace="pre-wrap">{d.descII}</Text>
                      </Box>
                    )}
                  </TabPanel>
                ))}
              </TabPanels>
            </Tabs>
          </Media>
        </Box>
      </Flex>
      <ArrowDown fullpageApi={fullpageApi} />
    </Box>
  )
}

export default Detail
