import React, { useContext, useEffect, useMemo, useRef, useState } from 'react'
import { IoIosArrowDown } from "react-icons/io";
import ReactFullpage from '@fullpage/react-fullpage';
import { AnimatePresence, motion } from "framer-motion"

import Box from '../../components/Box'
import Button from '../../components/Button'
import BackgroundImage from '../../components/BackgroundImage'
// import FullpageVerticalCenter from '../../components/FullpageVerticalCenter'
import { Media, responsive } from '../../contexts/responsive';
import useHeader from '../../contexts/header/useHeader';

import bg from './bg.png'
import bg_mobile from './bg_mobile.png'
import title from './title.png'

import Intro from './Intro';
import Preface from './Preface';
import Animals from './Animals';
import Care from './Care';
import IntroPlan from './IntroPlan';
import Data from './data';
import Detail from './Detail';
import Equipment from './Equipment';
import Step from './Step';
import Survey from './Survey';
import Epilogue from './Epilogue';
import Loading from './Loading';

const anchors = ["", "preface", "intro", "aniamls", "care", "plan", "data", "detail", "equipment", "step", "survey", "epilogue"];

const variants = {
  visible: { opacity: 1, x: [0, 100, 0] },
  hidden: { opacity: 0 },
}

const pages = [Preface, Intro, Animals, Care, IntroPlan, Data, Detail, Equipment, Step, Survey, Epilogue]

const CoverPage = ({ fullpageApi, onLoad }) => {
  // const [show, setShow] = useState()
  // useEffect(() => setTimeout(() => setShow(false), 1000), [show])
  return (
    <Box.Relative className="section">
      <Box.Absolute as={Media} greaterThanOrEqual="md" top="0" left="0" right="0">
        <BackgroundImage src={bg} height="100vh" ratio={1920 / 1080} onLoad={onLoad} />
      </Box.Absolute>
      <Box.Absolute as={Media} lessThan="md" top="0" left="0" right="0">
        <BackgroundImage backgroundPosition="71% 50%" src={bg_mobile} height="100vh" ratio={768 / 736} onLoad={onLoad} />
      </Box.Absolute>
      <Box.Absolute top="11.85%" left={responsive('52%', "8.7%")} transform={responsive('translateX(-50%)', 'none')} width={responsive('89%', "45.2%")}>
        <BackgroundImage src={title} ratio={1735 / 961} />
      </Box.Absolute>
      <Box.Absolute left={responsive('10%', '11%')} bottom={responsive('45%', '12%')}>
        <Button.Outline
          onClick={() => {
            fullpageApi.moveSectionDown()
            // setShow(true)
          }}
          fontSize={responsive('1em', '2.25em')}
          p={responsive('0em 1.25em 0em 1em', '0em 2.875em 0em 2.25em')}
          py={responsive('0.25em', '0.5625em')}
          iconSpacing="0"
          leftIcon={<IoIosArrowDown size="1.5em" />}
        >
          往下滑查看完整介紹
        </Button.Outline>
      </Box.Absolute>
    </Box.Relative>
  )
}

const IndexPage = ({ location }) => {
  const fpApi = useRef()
  const [show, setShow] = useState()
  const [loaded, setLoaded] = useState()
  const { setHeaderColor } = useHeader()
  useEffect(() => setTimeout(() => setShow(false), 1000), [show])

  useEffect(() => {
    if (fpApi.current) {
      fpApi.current.moveTo(location.hash.substr(1))
    }
  }, [location.hash])
  return (
    <>
      {/* <motion.div
        animate={{ x: [0, 100, 0] }}
        initial={false}
      >
        <Box bg="white" p="2em">我是波浪</Box>
      </motion.div> */}
      <ReactFullpage
        anchors={anchors}
        //fullpage options
        licenseKey={process.env.GATSBY_FULLPAGE_JS_KEY}
        // onLeave={(origin, destination, direction) => {
        //   console.log(origin)
        //   //after leaving section 2
        //   if(origin.index == 1 && direction =='down'){
        //     fpApi.current.setAllowScrolling(false, 'down')
        //   }
        // }}
        scrollingSpeed={1000}
        render={({ state, fullpageApi }) => {
          fpApi.current = fullpageApi
          if(fullpageApi?.getActiveSection().index > 6 && fullpageApi?.getActiveSection().index < 11) {
            setHeaderColor('black')
          } else {
            setHeaderColor('white')
          }
          return (
            <ReactFullpage.Wrapper>
              <CoverPage onLoad={() => setLoaded(true)} fullpageApi={fullpageApi} />
              {pages.map((Comp, i) => (
                <Comp key={i} fullpageApi={fullpageApi} isActive={fullpageApi?.getActiveSection().index == (i + 1)} anchor={anchors[i+1]} />
              ))}
              {/* <Preface fullpageApi={fullpageApi} anchor="preface" />
              <Intro index={3} fullpageApi={fullpageApi} anchor="intro" />
              <Animals anchor="animals" fullpageApi={fullpageApi} isActive={fullpageApi?.getActiveSection().index == 3}/>
              <Care anchor="care" fullpageApi={fullpageApi} />
              <IntroPlan anchor="plan" fullpageApi={fullpageApi} />
              <Data /> */}
            </ReactFullpage.Wrapper>
          );
        }}
      />
      <AnimatePresence>
        {!loaded && (
          <Loading />
        )}
      </AnimatePresence>
    </>
  )
}

export default IndexPage
