import React from 'react'
import { IconButton } from '@chakra-ui/react'
import { MdKeyboardArrowDown } from "react-icons/md";

import Box from './Box'
import { Media, responsive } from '../contexts/responsive'

const ArrowDown = ({ fullpageApi, bottom }) => {
  return (
    <Box.Absolute bottom={bottom || "5%"} left="50%" transform="translateX(-50%)">
      <IconButton
        isRound
        width={responsive('1.625em', '3.625em')}
        height={responsive('1.625em', '3.625em')}
        minWidth="auto"
        bg="white"
        color="black"
        onClick={() => fullpageApi.moveSectionDown()}
        icon={(
          <>
            <Media lessThan='md'>
              <MdKeyboardArrowDown size={'2em'} />
            </Media>
            <Media greaterThanOrEqual='md'>
              <MdKeyboardArrowDown size={'4em'} />
            </Media>
          </>
        )}
        _hover={{
          bg: 'white'
        }}
        _active={{
          bg: 'white'
        }}
      />
    </Box.Absolute>
  )
}

export default ArrowDown
